<template>
    <div>
        <h1>{{id?'':''}}</h1>
        <el-form @submit.native.prevent="save">
            <el-form-item label="">
                <el-input v-model="model.username"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="model.password"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" native-type="submit"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
    props:{
        id:{}
    },
    data(){
        return{
            model:{},
        }
    },
    methods:{
        async save(){     //save
            let res
            if(this.id){
                res = await this.$http.put(`rest/adminUser/${this.id}`,this.model)
            }else{
                res = await this.$http.post('rest/adminUser',this.model)
            }
            this.$router.push('/adminUser/AdminList')     //list
            this.$message({
            type:'success',
            message:''
          })
        },
        async fetch(){  //fetch
            const res = await this.$http.get(`rest/AdminUser/${this.id}`)
            this.model=res.data
        },
    },
    created(){
        this.id && this.fetch()       //idfetch
    }
}
</script>
